.pokeView {
  background: rgba(8, 8, 8, 0.349) !important;
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: grid;
  grid-template-columns: 1fr 660px;
  z-index: 4000; }
  .pokeView .container-pokemon {
    float: right;
    max-width: 660px !important;
    width: 100% !important;
    /*background-color: rgb(140, 221, 18) !important;*/
    display: grid !important;
    grid-template-rows: 55vh 1fr !important;
    grid-template-columns: 1fr !important;
    justify-content: center;
    align-items: center;
    gap: 0 !important;
    padding: 0 !important; }
    .pokeView .container-pokemon .top-part {
      height: 100% !important; }
      .pokeView .container-pokemon .top-part .header {
        height: 90px !important; }
        .pokeView .container-pokemon .top-part .header .poke-info {
          padding: 0 20px !important; }
          .pokeView .container-pokemon .top-part .header .poke-info .types .types-item {
            background: rgba(240, 238, 238, 0.226) !important;
            padding: 4px 12px;
            margin: 8px;
            border-radius: 14px;
            color: #fff;
            font-size: 14px;
            font-weight: 200; }
        .pokeView .container-pokemon .top-part .header .id-pokemon {
          margin-top: -20px; }
      .pokeView .container-pokemon .top-part .image-container {
        height: 60% !important; }
        .pokeView .container-pokemon .top-part .image-container img {
          height: 130% !important;
          margin-top: -60px !important;
          transform: translateZ(30px) !important; }
  .pokeView .bottom-part {
    border-radius: 35px 35px 0 0;
    box-shadow: rgba(34, 34, 34, 0.034) 0px -5px 5px;
    background: #f8f8f8 !important;
    padding-top: 30px;
    padding-bottom: 20px;
    height: 100% !important; }
    .pokeView .bottom-part nav ul {
      color: red;
      width: 70%;
      margin: auto;
      display: grid;
      grid-template-columns: 1fr 1fr;
      padding: 0 !important;
      cursor: pointer; }
      .pokeView .bottom-part nav ul li {
        width: 100%;
        height: 100%;
        list-style: none;
        text-align: center;
        padding-bottom: 10px !important;
        color: #616161;
        font-size: 15px; }
      .pokeView .bottom-part nav ul .selected-normal {
        color: #f5d25e !important;
        border-bottom: 3px solid #f5d25e !important; }
      .pokeView .bottom-part nav ul .selected-fire, .pokeView .bottom-part nav ul .selected-fairy, .pokeView .bottom-part nav ul .selected-fighting {
        color: #ff2020 !important;
        border-bottom: 3px solid #ff2020 !important; }
      .pokeView .bottom-part nav ul .selected-water {
        color: #6fc0b7 !important;
        border-bottom: 3px solid #6fc0b7 !important; }
      .pokeView .bottom-part nav ul .selected-grass, .pokeView .bottom-part nav ul .selected-bug {
        color: #07a05d !important;
        border-bottom: 3px solid  #07a05d !important; }
      .pokeView .bottom-part nav ul .selected-poison {
        color: #b424b4 !important;
        border-bottom: 3px solid #b424b4 !important; }
      .pokeView .bottom-part nav ul .selected-dark, .pokeView .bottom-part nav ul .selected-ghost {
        color: #6e6d6d !important;
        border-bottom: 3px solid #6e6d6d !important; }
      .pokeView .bottom-part nav ul .selected-flying {
        color: rgba(88, 87, 87, 0.664);
        border-bottom: 3px solid rgba(88, 87, 87, 0.664); }
      .pokeView .bottom-part nav ul .selected-psychic {
        color: #cc4c70;
        border-bottom: 3px solid #cc4c70; }
      .pokeView .bottom-part nav ul .selected-electric {
        color: #dbdf17 !important;
        border-bottom: 3px solid #dbdf17 !important; }
      .pokeView .bottom-part nav ul .selected-ice {
        color: #09e5f5 !important;
        border-bottom: 3px solid #09e5f5 !important; }
      .pokeView .bottom-part nav ul .selected-ground {
        color: #eeca00;
        border-bottom: 3px solid #eeca00; }
      .pokeView .bottom-part nav ul .selected-rock {
        color: #7252A7;
        border-bottom: 3px solid #7252A7; }
    .pokeView .bottom-part .view-about {
      margin: auto;
      margin-top: 10px;
      width: 70% !important; }
      .pokeView .bottom-part .view-about .grid {
        display: grid;
        grid-template-columns: 200px 1fr;
        color: #5c5b5b !important;
        font-size: 15px; }
        .pokeView .bottom-part .view-about .grid ul {
          margin: 0;
          padding: 5px; }
          .pokeView .bottom-part .view-about .grid ul li {
            list-style: none;
            margin: 0 !important;
            padding: 0 !important; }
        .pokeView .bottom-part .view-about .grid .about-value {
          color: #1a1919 !important;
          font-weight: 500; }
    .pokeView .bottom-part .evolutions-container {
      display: flex;
      justify-content: center;
      margin: 0;
      padding: 35px 0px 0px 0 !important; }
      .pokeView .bottom-part .evolutions-container .container-card {
        width: 210px;
        display: flex;
        flex-direction: row-reverse;
        align-items: center;
        justify-content: flex-start;
        position: relative; }
        .pokeView .bottom-part .evolutions-container .container-card .next-evolution {
          color: #bebdbd;
          font-size: 70px;
          padding: 0 !important;
          text-align: left;
          margin-left: 0px;
          position: absolute;
          left: -36px; }
        .pokeView .bottom-part .evolutions-container .container-card .card-evolution {
          margin: auto;
          width: 150px;
          height: 150px;
          border-radius: 50% !important;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          border: 5px solid #e6e5e5 !important;
          box-shadow: #555 -1px 5px 5px; }
          .pokeView .bottom-part .evolutions-container .container-card .card-evolution img {
            width: 70%; }

#btn-back {
  cursor: pointer;
  font-size: 30px !important;
  font-weight: 500;
  color: #fff;
  margin: 10px 0px 0px 15px;
  opacity: 0.8;
  padding: 5px 13px;
  border-radius: 17px;
  background: rgba(240, 238, 238, 0.137) !important; }
  #btn-back:hover {
    opacity: 1;
    background: rgba(240, 238, 238, 0.226) !important; }

.pokeView .type-normal {
  background: radial-gradient(circle, #e9cd73, #ffcd28) !important; }

.pokeView .type-fire, .pokeView .type-fairy, .pokeView .type-fighting {
  background: #e92b25;
  background: radial-gradient(circle, #b61010f3 10%, #ff2020 100%) !important; }

.pokeView .type-water {
  background: #75bde0;
  background: #4181af !important;
  background-image: linear-gradient(15deg, #13547a 0%, #80d0c7 100%) !important; }

.pokeView .type-grass, .pokeView .type-bug {
  background-image: linear-gradient(to top, #07a05d 0%, #3cba92 100%) !important; }

.pokeView .type-poison {
  background: #b424b4 !important; }

.pokeView .type-dark, .pokeView .type-ghost {
  background: radial-gradient(circle, #6e6d6d, #555) !important; }

.pokeView .type-flying {
  background: radial-gradient(circle, #548380, rgba(255, 247, 247, 0.664)); }

.pokeView .type-psychic {
  background: radial-gradient(circle, #ecb7b7, #dd4470); }

.pokeView .type-electric {
  background: radial-gradient(circle, #dbdf17, #f8fc2c) !important; }

.pokeView .type-ice {
  background: #09e5f5 !important; }

.pokeView .type-ground {
  background: #eeca00; }

.pokeView .type-rock {
  background: #7252A7; }

@media (max-width: 600px) {
  .pokeView {
    grid-template-columns: 1fr;
    padding: 0 !important;
    height: 100vh !important; }
    .pokeView .container-pokemon {
      width: 100%;
      height: 100vh; }
    .pokeView .left-part {
      display: none; }
    .pokeView .poke-info {
      height: 80px; }
      .pokeView .poke-info .types.types-item {
        margin-left: 0 !important;
        margin-top: 0 !important; }
    .pokeView .bottom-part .view-about {
      width: 80% !important; }
      .pokeView .bottom-part .view-about .grid {
        grid-template-columns: 120px 1fr; }
    .pokeView .bottom-part .evolutions-container {
      flex-direction: column;
      align-items: center;
      padding: 20px 0px 70px 0 !important; }
      .pokeView .bottom-part .evolutions-container .container-card {
        width: 190px;
        height: 200px; }
        .pokeView .bottom-part .evolutions-container .container-card:nth-child(1), .pokeView .bottom-part .evolutions-container .container-card:nth-child(2) {
          margin-bottom: 30px !important; }
        .pokeView .bottom-part .evolutions-container .container-card .next-evolution {
          left: 30%;
          top: -50px;
          transform: rotate(90deg); }
        .pokeView .bottom-part .evolutions-container .container-card .card-evolution {
          width: 180px;
          height: 180px; } }
