*::-webkit-scrollbar {
  background-color: rgba(255, 255, 255, 0.13);
  width: 8px !important;
  border-radius: 5px !important;
  height: auto !important; }

*::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-image: linear-gradient(to bottom, #f83600 0%, #f9d423 100%); }

body {
  margin: 0 !important;
  padding: 0 !important;
  /*background-image: linear-gradient(to left, #9be15d 0%, #00e3ae 100%);
    background-image: linear-gradient(to BOTTOM, #3B7097 0%, #4A8DB7 100%);*/
  background: #4A8DB7;
  width: 100vw !important;
  font-family: 'Noto Sans JP', sans-serif;
  overflow: hidden !important;
  background-image: url("../background.png"); }
  body .container-fluid {
    overflow: auto !important;
    height: 100vh;
    width: 100vw;
    padding: 0 !important;
    margin: 0 !important;
    /*background-image: linear-gradient(to left, #f83600 0%, #f9d423 100%);*/ }
    body .container-fluid .header {
      height: 200px;
      margin-top: 0px !important;
      padding: 20px 0 0 0 !important;
      margin: 0 !important; }
      body .container-fluid .header .title-header {
        height: 150px; }
    body .container-fluid .container {
      width: 100% !important;
      max-width: 1200px !important;
      margin: 0 !important;
      margin: auto !important;
      padding: 0 !important;
      padding-bottom: 30px !important; }

#loading {
  position: absolute;
  top: 42vh;
  left: 45vw;
  z-index: 3000 !important;
  height: 100px;
  width: 120px;
  border-radius: 6px;
  background-color: rgba(255, 255, 255, 0.13);
  backdrop-filter: blur(3px);
  box-shadow: rgba(88, 85, 85, 0.24) -3px 5px 10px;
  transform: translateZ(30px) !important;
  transition: ease-in-out 1s; }

/* RESPONSIVE DESIGN */
@media (max-width: 550px) {
  .container-fluid .header {
    position: relative; }
    .container-fluid .header .title-header {
      width: 70%;
      height: 60% !important; }
  #loading {
    left: 35vw; } }
